.CurrentTemp {
    height: 100vh;
    width: 30vw;
    background-color: #1E213A;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: white;
    align-items: center;
    overflow: hidden;
    font-family: Raleway, 'sans-serif';
}

.searchCurrent {
    justify-content: flex-start !important;
}

.search-btn {
    margin-top: 25px;
    width: 80%;
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.search-btn button {
    background-color: #6E707A;
    outline: none;
    border: none;
    font-size: 1vw;
    padding: 0.8vw;
    filter: drop-shadow(0 2px 2px #000000);
    transition: all 0.2s;
    color: #E7E7EB;
    cursor: pointer;
}

.search-btn button:active {
    outline: none;
    border: none;
}

.get-location {
    max-width: 2.8vw;
    max-height: 2.8vw;
}

.get-location img {
    filter: drop-shadow(0 2px 2px #000000);
    background-color: #6E707A;
    border-radius: 100%;
    padding: 0.35vw;
    max-width: 100%;
    max-height: 100%;
    cursor: pointer;
}

.current-image {
    background-image: url('../Images/Cloud-background.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 35vh;
}

.current-image div {
    opacity: 0.9;
    background-color: #1E213A;
    width: 100%;
    height: 35vh;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.current-image img {
    width: 11vw;
    max-width: 11vw;
}


.current-temp {
    margin-top: 2vw;
    display: flex;
    align-items: center;
    align-items: baseline;
}

.current-temp p {
    font-size: 7vw;
}

.current-temp div {
    color: #A09FB1;
    align-self: center;
}
 
#o {
    font-size: 1.5vw;
}

#c {
    font-size: 3vw;
    position: relative;
    top: 2vw;
}


.current-weather {
    margin-top: 3vw;
    font-size: 2.5vw;
    color: #A09FB1;
}

.current-date {
    margin-top: 3vw;
    color: #A09FB1;
    display: flex;
    align-items: center;
    font-size: 1.2vw;
}

#date-point {
    display: flex;
    align-items: center;
}

#point {
    width: 3px;
    height: 3px;
    background-color: #A09FB1;
    border-radius: 100%;
    margin: 1vw;
}

.current-location {
    margin-top: 2.4vw;
    color: #A09FB1;
    font-size: 1.2vw;
    display: flex;
    align-items: center;
    margin-bottom: 1vw;
}

.current-location img {
    max-width: 1.5vw;
}

.search-bar {
    width: 80%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
}

.crossBtn {
    width: 100%;
    margin-bottom: 2.5vw;
    margin-top: 2vw;
}

.crossBtn span {
    cursor: pointer;
    position: relative;
    left: 90%;
    font-size: 2.5vw;
}

#search-icon {
    top: 8.2vw;
    left: 0.4vw;
    position: absolute;
}

#search-icon span {
    font-size: 1.5vw;
}

.search-bar input {
    color: #A09FB1;
    padding: 1vw 2vw;
    font-size: 1vw;
    outline: none;
    background-color: transparent;
    border: 1px solid #A09FB1;
}

.search-bar button {
    font-size: 1vw;
    cursor: pointer;
    color: white;
    border: none;
    outline: none;
    padding: 1vw;
    background-color: #3C47E9;
}

@media screen and (max-width: 1150px) {

    .CurrentTemp {
        max-width: 350px;
        min-width: 300px;
    }
    .search-btn button {
        padding: 11px;
        font-size: 18px;
    }

    .get-location {
        max-width: 50px;
        max-height: 50px;
    }

    .get-location img {
        max-width: 100%;
        max-height: 100%;
        padding: 5px;
    }


    .current-image img {
        width: 165px;   
    }

    .current-temp {
        margin: 0;
    }

    .current-temp p {
        font-size: 90px;
    }

    #o {
        font-size: 23px;
    }

    #c {
        font-size: 47px;
        top: 25px;
    }

    .current-weather {
        font-size: 28px;
    }

    .current-date {
        font-size: 19px;
    }

    .current-location {
        font-size: 23px;
    }

    .current-location img {
        max-width: 30px;
    }

    .crossBtn {
        margin: 22px 0 23px 0;
    }

    .crossBtn span {
        font-size: 28px;
    }

    #search-icon {
        top: 89px;
        left: 5px;
    }

    #search-icon span {
        font-size: 18px;
    }


    .search-bar input {
        width: 170px;
        font-size: 13px;
        padding: 11px 0;
        padding-left: 25px;
    }

    .search-bar button {
        font-size: 13px;
        padding: 11px;
    }

}

@media screen and (max-width: 600px) {
    .CurrentTemp {
        justify-content: space-around;
        height: 80vh;
        width: 100%;
        max-width: 100%;
    }

    .search-btn button {
        padding: 3.3vw;
        font-size: 2.9vw;
    }

    .get-location {
        max-width: 11vw;
        min-width: 11vw;
    }


    .current-image img {
        max-width: 35vw;
    }

    .current-location {
        margin: 30px;
    }

    .current-location span {
        font-size: 15px;
    }

    .current-location img {
        max-width: 24px;
    }

    .search-bar {
        width: 95%;
    }
    .search-bar input{
        width: 65vw;
    }

    .search-bar button {
        font-size: 14px;

    }
}



