.others {
    top: 4vw;
    position: relative;
    width: 60vw;
    color: #E7E7EB;
    font-family: Raleway, 'sans-serif';
}

.heading-high {
    margin-left: 2vw;
    width: 100%;
    font-size: 1.8vw;
}

.other-detail {
    width: 60vw;
    display: flex;
    flex-wrap: wrap;
}

.other {
    background-color: #1E213A;
    width: 25vw;
    margin: 2vw;
    text-align: center;
    padding-top: 1.3vw;
    padding-bottom: 0.7vw;
}

.other p {
    margin-bottom: 1vw;
    font-size: 1vw;
}

.other h2 {
    font-size: 3vw;
}

.other span {
    font-size: 1.7vw;
}

.compass {
    width: 50%;
    margin: 1vw auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.compass-arrow {
    background-color: #E7E7EB;
    border-radius: 100%;
    width: 2vw;
    height: 2vw;
    position: relative;
    transform: rotate(-44deg);
}

.compass p {
    margin-bottom: 0;
}

.compass-arrow img {
    position: absolute;
    max-width: 70%;
    transform: translateX(-50%);
    left: 50%;
    top: 13%;
}

.N {
    transform: rotate(-45deg);
}

.NNE {
    transform: rotate(-22.5deg);
}

.NE {
    transform: rotate(0deg);
}

.ENE {
    transform: rotate(22.5deg);
}

.E {
    transform: rotate(45deg);
}

.ESE {
    transform: rotate(67.5deg);
}

.SE {
    transform: rotate(90deg);
}

.SSE {
    transform: rotate(112.5deg);
}

.S {
    transform: rotate(135deg);
}

.SSW {
    transform: rotate(157.5deg);
}

.SW {
    transform: rotate(180deg);
}

.WSW {
    transform: rotate(202.5deg);
}

.W {
    transform: rotate(225deg);
}

.WNW {
    transform: rotate(247.5deg);
}

.NW {
    transform: rotate(270deg);
}

.NNW {
    transform: rotate(292.5deg);
}

.humidity-bar {
    width: 80%;
    margin: 0 auto;
}

.hum-read {
    margin-top: 1vw;
    margin-bottom: 0.3vw;
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.hum-read span {
    font-size: 1vw;
}

.hum-fill {
    width: 100%;
    height: 0.5vw;
    background-color: white;
    border-radius: 0.5vw / 100%;
    overflow: hidden;
    margin-bottom: 1vw;
}

.hum-color {
    height: 0.5vw;
    border-radius: 0.5vw / 100%;
    background-color: yellow;
}


@media screen and (max-width: 1150px) {
    .others {
        align-self: center;
    }

    .heading-high {
        font-size: 2.5vw;
        margin-left: 0.5vw;
        margin-bottom: 10px;
    }

    .other-detail {
        width: 100%;
    }

    .other {
        width: 46%;
        margin: 5px;
        height: 180px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }

    .other p {
        font-size: 2.1vw;
        margin-bottom: 0;
    }

    .other h2 {
        font-size: 4.8vw;
    }

    .other span {
        font-size: 2.1vw;
    }

    .compass-arrow {
        width: 3vw;
        height: 3vw;
    }

    .compass-arrow img {
        max-width: 80%;
    }

    .hum-read span {
        font-size: 1.8vw; 
    }

    .hum-fill {
        height: 1vw;
        border-radius: 1vw / 100%;
    }

    .hum-color {
        height: 1vw;
    }
}

@media screen and (max-width: 600px) {
    .others {
        width: 270px;
    }

    .heading-high {
        font-size: 18px;
    }

    .other-detail {
        display: flex;
        flex-direction: column;
    }

    .other {
        width: 100%
    }

    .other p {
        font-size: 15px;
    }

    .other h2 {
        font-size: 45px;
    }

    .other span {
        font-size: 27px;
    }

    .compass {
        width: 30%;
    }

    .compass-arrow {
        width: 20px;
        height: 20px;
    }

    .hum-read span {
        font-size: 12px;
    }

    .hum-fill {
        height: 10px;
        border-radius: 10px / 100%;
    }

    .hum-color {
        height: 10px;
    }
}