.futureDays {
    width: 90%;
}

.futureDays li {
    display: inline-block;
    font-family: Raleway, 'sans-serif';
    color: #A09FB1;
}

.futureDay {
    width: 9vw;
    height: 12vw;
    margin: 1.25vw;
    background-color: #1E213A;
    text-align: center;
    padding: 1vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.futureDay p {
    font-size: 1.2vw;
}

.futureDay img {
    max-width: 4vw;
}

.minMax {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 0.9vw;
}

.o {
    font-size: 0.4vw;
    margin: 1px;
    position: relative;
    bottom: 0.3vw;
}

.c {
    position: relative;
    top: 0.1vw;
}

@media screen and (max-width: 1150px) {
    .futureDays {
        margin-right: 2vw;
        width: 95%;
        align-self: center;
        display: flex;
        justify-content: space-evenly;
    }

    .futureDays li {
        display: block
    }

    .futureDay {
        margin: 2.5px;
        width: 10vw;
        height: 150px;
    }

    .futureDay p {
        margin-top: 10px;
        font-size: 1.4vw;
    }

    .futureDay img {
        max-width: 5vw;
    }

    .minMax {
        margin-bottom: 10px;
        font-size: 1.8vw;
    }

    .o {
        font-size: 0.7vw;
    }

    .c {
        font-size: 1vw;
    }
}

@media screen and (max-width: 600px) {
    .futureDays {
        width: 210px;
        margin: 30px 0;
        flex-wrap: wrap;
        justify-content: start;
    }

    .futureDay {
        margin: 1vw;
        max-width: 110px;
        min-width: 90px;
    }

    .futureDay p {
        font-size: 11px;
    }

    .futureDay img {
        max-width: 40px;
    }

    .minMax {
        width: 85%;
        font-size: 15px;
    }

    .o {
        font-size: 8px;
        bottom: 6px;
    }

    .c {
        font-size: 11px;
    }
}


