
.loadC {
    display: flex;
    align-items: center;
    justify-content: center;
}

.load-one {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    height: 12vw;
    padding: 0;
}

.load-two {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    padding: 0;
    height: 8vw;
}

.loadF {
    display: flex;
    align-items: center;
    justify-content: center !important;
    padding: 0;
    overflow: hidden;
}

.white {
    width: 4.5vw;
    display: flex;
    justify-content: space-between;
}

.ball-one {
    position: relative;
    border-radius: 100%;
    width: 1vw;
    height: 1vw;
    background-color: rgb(26, 26, 236);
    animation: ball-one 1s linear infinite;
}

.ball-two {
    position: relative;
    border-radius: 100%;
    width: 1vw;
    height: 1vw;
    background-color: rgb(25, 162, 216);
    animation: ball-one 1s linear 0.3s infinite;
}

.ball-three {
    position: relative;
    border-radius: 100%;
    width: 1vw;
    height: 1vw;
    background-color: rgb(255, 255, 255);
    animation: ball-one 1s linear 0.6s infinite;
}


@keyframes ball-one {
    0% {
        bottom: 0;
    }

    50% {
        bottom: 1.5vw;
    }

    100% {
        bottom: 0;
    }
}

@media screen and (max-width: 1150px) {
    .load-one {
        height: 180px;
    }

    .load-two {
        height: 180px;
    }
}

@media screen and (max-width: 600px) {
    .white {
        width: 6.5vw;
    }
    .ball-one, .ball-two, .ball-three {
        width: 2vw;
        height: 2vw;
    }
}