.error {
    font-family: Raleway, 'sans-serif';
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 3vw;
    color: white;
    background-color: #1E213A;
    padding: 4vw;
}

.error span {
    position: absolute;
    top: 0.8vw;
    right: 0.8vw;
    font-size: 2vw;
    cursor: pointer;
}

@media screen and (max-width: 600px) {
    .error {
        width: 70vw;
        font-size: 6vw;
    }

    .error span {
        font-size: 4.5vw;
    }
}